import React from 'react';
import PropTypes from 'prop-types';

const alert = (props) => {
    let typeClasses = "text-blue-800 border border-blue-300 bg-blue-50 dark:bg-gray-800 dark:text-blue-400 dark:border-blue-800";
    let srTitle = 'Info';

    switch (props.type) {
        case 'success':
            typeClasses = "text-green-800 border border-green-300 bg-green-50 dark:bg-gray-800 dark:text-green-400 dark:border-green-800";
            srTitle = 'Success';
            break;
        case 'error':
            typeClasses = "text-red-800 border border-red-300 bg-red-50 dark:bg-gray-800 dark:text-red-400 dark:border-red-800";
            srTitle = 'Error';
            break;
        default:
            break;
    }

    return (
        <div onClick={props.onClick} className={`flex p-4 mb-4 text-sm rounded-lg ${typeClasses} ${props.className}`} role="alert">
            <svg aria-hidden="true" className="flex-shrink-0 inline w-5 h-5 mr-3" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd"></path></svg>
            <span className="sr-only">{srTitle}</span>
            <div>
                {props.children}
            </div>
        </div>
    );
}

alert.propTypes = {
    onClick: PropTypes.func,
    type: PropTypes.string,
    className: PropTypes.string,
}

alert.defaultProps = {
    type: 'info',
    className: '',
    onClick: (_) => {
    }
}

export default alert;