import React from 'react';
import { useQuery } from 'react-query';
import Button from './../../components/ui/Button';
import Alert from './../../components/ui/Alert';

import { encrypt } from './../../support/utility';

import './../../App.css';
import axios from 'axios';

function CrackMePage() {

  const [userInput, setUserInput] = React.useState('');
  const [validated, setValidated] = React.useState(false);
  const [valid, setValid] = React.useState(false);

  const { isLoading, error, refetch } = useQuery('init', async () => {
    let res = await axios.get('/api/crack-me');
    window.crackMe = res.data;
    return res.data;
  });

  const validateUserInput = (userInput) => {
    if (typeof (window.crackMe) === 'undefined' || !window.crackMe) {
      return false;
    }

    if (!Array.isArray(window.crackMe)) {
      return false;
    }

    if (window.crackMe.length < 3) {
      return false;
    }

    const enc = encrypt(userInput);
    return enc === window.crackMe[2];
  }

  const onClick = () => {
    setValidated(true);
    setValid(validateUserInput(userInput));
  }

  const onErrorClick = () => {
    refetch();
  }

  const inputOnChange = (event) => {
    setValidated(false);
    setUserInput(event.target.value ?? '');
  }

  let inputClasses = '';
  let inputLabelClasses = '';
  let inputHelpClasses = '';

  if (validated) {
    if (valid) {
      inputClasses = 'border-green-600 dark:border-green-500 dark:focus:border-green-500 focus:border-green-600 peer';
      inputLabelClasses = 'text-green-600 dark:text-green-500';
      inputHelpClasses = 'text-green-600 dark:text-green-400';
    } else {
      inputClasses = 'dark:border-red-500 border-red-600 focus:border-red-600 dark:focus-border-red-500';
      inputLabelClasses = 'text-red-600 dark:text-red-500';
      inputHelpClasses = 'text-red-600 dark:text-red-400';
    }
  }

  let content = (
    <>
      <h1 className="text-5xl">
        Crack me!
      </h1>

      <div className='py-5'>
        <p className='mb-3 text-lg text-center'>
            Your task is to brute force the flag.
            <br/>
            Be clever!
        </p>
        <div className="relative">
          <input value={userInput} onChange={inputOnChange} type="text" id="flag-input" aria-describedby="flag-input-help" className={`block rounded-t-lg px-2.5 pb-2.5 pt-5 w-full text-sm text-white bg-gray-700 border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 peer ${inputClasses} ${validated && !valid ? 'Shake': ''}`} placeholder=" " />
          <label htmlFor="flag-input" className={`absolute text-sm duration-300 transform -translate-y-4 scale-75 top-4 z-10 origin-[0] left-2.5 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-4 ${inputLabelClasses}`}>Flag!</label>
        </div>
        {validated && (
          <p id="flag-input-help" className={`mt-2 text-xs ${inputHelpClasses}`}><span className="font-medium">{valid ? 'Well done!' : 'Try again!  '}</span></p>
        )}
      </div>

      <Button onClick={onClick}>
        Test
      </Button>

    </>
  );

  if (isLoading) {
    content = (
      <Alert>
        Loading data ...
      </Alert>
    );
  }

  if (error) {
    content = (
      <Alert className='cursor-pointer' onClick={onErrorClick} type={'error'}>
        <span className="font-medium">Error!</span> Something went wrong! click here to try again
      </Alert>
    );
  }

  return (
    <div className="App">
      <header className="App-header">
        {content}
      </header>
    </div>
  );
}

export default CrackMePage;
