import React from 'react';
import { Routes, Route } from "react-router-dom";
import CrackMePage from './containers/CrackMe/CrackMePage';
import FindMePage from './containers/FindMe/FindMePage';
import Home from './containers/Home';

function App() {
  return (
    <Routes>
        <Route path="/" element={<Home />}/>
        <Route path="/crack-me" element={<CrackMePage />}/>
        <Route path="/find-me" element={<FindMePage />}/>
        <Route path='*' element={<Home />}/>
    </Routes>
  );
}

export default App;
