import crypto from 'crypto';
import {Buffer} from 'buffer';

window.Buffer = Buffer;

export const encrypt = (str) => {
    const key = window.crackMe ? Buffer.from(window.crackMe[0], 'base64') : new Buffer(crypto.randomBytes(32), 'utf8');
    const iv = window.crackMe ? Buffer.from(window.crackMe[1], 'base64'): new Buffer(crypto.randomBytes(16), 'utf8');
    const cipher = crypto.createCipheriv('AES-256-CBC', key, iv);

    let enc = cipher.update(str, 'utf8', 'base64');
    enc += cipher.final('base64');
    return enc;
};

export const getResponseErrorMessage = (e) => {
    const message = "Network Error!";

    if (e && e.response && e.response.data) {
        if (e.response.data.message) {
            return e.response.data.message;
        }
    }

    return message;
}