import React from 'react';
import { useMutation } from 'react-query';
import Button from '../../components/ui/Button';
import Alert from '../../components/ui/Alert';

import './../../App.css';
import axios from 'axios';
import { getResponseErrorMessage } from '../../support/utility';

function FindMePage() {

  const { data, isSuccess, isLoading, error, mutate, reset} = useMutation(async (file) => {
    let formData = new FormData();
    formData.append('file', file);

    const response = await axios.post("/api/media/upload", formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });

    setSelectedFile(null);
    inputRef.current.value = "";

    return response.data;
  }, {
    mutationKey: "upload",
  });

  const inputRef = React.useRef(null);
  const [selectedFile, setSelectedFile] = React.useState();
  const [validationError, setValudationError] = React.useState(null);

  const validateUserInput = () => {
    if (!selectedFile) {
      setValudationError("File is required");
      return false;
    }

    if (selectedFile.size >= 102400) {
      setValudationError("File size must be less than 100k");
      return false;
    }

    return true;
  }

  const onClick = () => {
    reset();
    setValudationError(null);
    let isValid = validateUserInput();

    if (isValid) {
      mutate(selectedFile);
    }
  }

  const fileOnChange = (event) => {
    setSelectedFile(event.target.files[0]);
  }

  let content = (
    <>
      <h1 className="text-5xl">
        Find me!
      </h1>

      <div className='py-5'>
        <p className='mb-3 text-lg text-center'>
          Flag is hidden somewhere on this page!
        </p>
        <div className="relative">

          <input ref={inputRef} onChange={fileOnChange} className={`relative block text-sm text-gray-900 
          border border-gray-300 rounded-lg 
          cursor-pointer bg-gray-50 dark:text-gray-400 
          focus:outline-none dark:bg-gray-700 dark:border-gray-600 
          dark:placeholder-gray-400  
          file:overflow-hidden file:rounded-none file:border-0
          file:border-solid file:border-inherit file:p-3 file:mr-3`} type="file" />

        </div>
      </div>

      {validationError && (
        <Alert type={'error'}>
          <span className="font-medium">Error!</span> {validationError}
        </Alert>
      )}

      {error && (
        <Alert type={'error'} onClick={onClick}>
          <span className="font-medium">Error!</span> {getResponseErrorMessage(error)}
          <br />
          Click here to try again!
        </Alert>
      )}

      {(data || isSuccess) && (
        <Alert type={'success'} onClick={onClick}>
          <span className="font-medium">Success!</span> File uploaded successfully!
        </Alert>
      )}

      <Button loading={isLoading} onClick={onClick}>
        Upload
      </Button>

    </>
  );

  return (
    <div className="App">
      <header className="App-header">
        {content}
      </header>
    </div>
  );
}

export default FindMePage;
